.header {
  z-index: 10;
  height: fit-content;
  width: 100%;
  background: var(--bg-body-color);
  position: sticky;
  top: 0;
}

.headerContent {
  position: relative;
  margin: 1rem 0;
  display: inline-flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 3.5rem;
}

.logo {
  display: inline-flex;
  object-fit: cover;
  object-position: 0 5px;
}

@media screen and (max-width: 768px) {
  article {
    scroll-margin-block-start: 0;
  }
}
